<template>
  <div class="box">
    <div style="display: flex;justify-content: space-between;">
      <div>已合作渠道商列表</div>
      <el-button @click="newlyAdded.dialogVisible = true" type="primary" icon="el-icon-search">新增渠道商户</el-button>
    </div>
    <el-table border class="new-table-fixed-right new-table-fixed" ref="multipleTable" :data="tableData"
      tooltip-effect="dark" :row-style="{'height':'10px'}" :cell-style="{'padding':'5px 0px'}"
      :header-cell-style="headerStyle">
      <el-table-column type="index" label="序号" width="80">
      </el-table-column>
      <el-table-column v-for="(item, index) in tableMsg" :key="index" :prop="item.key" :label="item.name">
        <template slot-scope="scope">
          <div v-if="scope.row.bmp_type==2&&item.name=='登录账号'">
            -
          </div>
          <div v-else>
            {{scope.row[item.key]}}
          </div>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="操作" width="320">
        <template slot-scope="scope">
          <el-button @click="PopupControl(scope,merchantInformation,'商户信息')" type="text" size="small">商户信息</el-button>
          <!-- <el-button @click="StateManagement.dialogVisible = true" type="text" size="small">数据查看</el-button> -->
          <el-button @click="equipmentGetData(scope.row)" type="text" size="small">设备管理</el-button>
          <!-- <el-button @click="StateManagement.dialogVisible = true" type="text" size="small">终止合作</el-button> -->
        </template>
      </el-table-column>
    </el-table>
    <!--page start 分页  current-page:当前页，handleSizeChange：每页显示条数改变的时候，page-size：每页显示条数，total：总条数，handleCurrentChange：下一页-->
    <el-pagination style="margin-top: 10px;" background @size-change="handleSizeChange"
      @current-change="handleCurrentChange" :current-page="1" :page-sizes="[10, 20, 50, 100,200]" :page-size="10"
      layout="total, sizes, prev, pager, next, jumper" :total="10">
    </el-pagination>

    <!-- 商户信息弹框 -->
    <el-dialog title="商户信息" :visible.sync="merchantInformation.dialogVisible" width="20%" :before-close="handleClose">
      <div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">商户名称：{{merchantInformation.obj.bmp_name}}
        </div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">联系人：
          {{merchantInformation.obj.bmp_contacts}}</div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">联系电话：
          {{merchantInformation.obj.bmp_phone}}</div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">银联子商户号： {{merchantInformation.obj.bmp_mid}}
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="merchantInformation.dialogVisible = false">关 闭</el-button>
      </span>
    </el-dialog>
    <!-- 弹出层 -->
    <el-dialog title="绑定设备" :visible.sync="equipmentManagement.dialogVisible" width="73%" :before-close="handleClose">
      <div slot="title" style="text-align: left;font-size:20px;font-weight: bold;">
        <span>{{ '设备管理' }}</span>
      </div>
      <div class="box test-5">
        <div style="text-align: left;font-size:18px;font-weight: bold;margin: 10px;">
          <span>终端列表</span>
        </div>
        <el-table border  class="new-table-fixed-right new-table-fixed" ref="multipleTable" align="center"
          :data="equipmentManagement.tableData" tooltip-effect="dark" :row-style="{'height':'10px'}"
          :cell-style="{'padding':'5px 0px'}" :header-cell-style="headerStyle" >
          <el-table-column type="index" label="序号" width="80" align="center">
          </el-table-column>
          <el-table-column prop="di_address_type" label="设备场地类型" align="center">
          </el-table-column>
          <el-table-column prop="di_uuid" label="设备编号" align="center"> 
            <template slot-scope="scope">
              <span>{{scope.row.di_uuid| format}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="di_setup_position" label="设备地址" align="center"> 
            <template slot-scope="scope">
              <span>{{scope.row.di_setup_position}}</span>
            </template>
          </el-table-column>
          <el-table-column label="绑定状态" align="center">
            <template slot-scope="scope">
              <span>{{scope.row.bmp_name?scope.row.bmp_name:"未绑定"}}</span>
            </template>
          </el-table-column>
          <el-table-column label="绑定设备" align="center">
            <template slot-scope="scope">
              <el-button v-if="scope.row.bmp_uuid==''?true:false" @click="bindingClick(scope)" type="text" size="small">
                绑定</el-button>
              <el-button v-if="scope.row.bmp_uuid == equipmentManagement.msg.bmp_uuid?true:false"
                @click="channelClick(scope)" type="text" size="small">解绑</el-button>
              <div style="text-align: center;"
                v-if="scope.row.bmp_uuid != equipmentManagement.msg.bmp_uuid&&scope.row.bmp_uuid!=''?true:false"
                >-</div>
            </template>
          </el-table-column>
          <el-table-column prop="bmp_mid" label="渠道服务费" align="center">
            <template slot-scope="scope">
              <div v-if="scope.row.bmp_uuid==''?true:false" style="text-align: center;">
                -
              </div>
              <div v-if="scope.row.bmp_uuid ==equipmentManagement.msg.bmp_uuid?true:false" style="text-align: left;">
                每笔交易额
                <el-input style="width: 150px;" @input='inputChange(scope.row,"mbmp_fee")' size="mini" v-model="scope.row.mbmp_fee" placeholder="请输入百分比交易额">
                </el-input>%
              </div>
              <div v-else style="text-align: center;">
                -
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="resetForm1('ruleForm')">关 闭</el-button>
        <el-button type="primary" @click="submitList('ruleForm')">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 新增商户弹框 -->
    <el-dialog :show-close="false" :close-on-press-escape="false" :close-on-click-modal="false"
      :visible.sync="newlyAdded.dialogVisible" width="600px">
      <div slot="title" style="text-align: left;font-size:20px;font-weight: bold;">
        <span>{{ '新增已审核商户' }}</span>
      </div>
      <div class="box1 test-5">
        <div slot="title" style="text-align: left;font-size:18px;font-weight: bold;margin-left: 10px;">
          <span>基本信息</span>
        </div>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="160px" class="demo-ruleForm">
          <el-row :gutter="24">
            <el-form-item label="商户名称:" prop="bmp_name">
              <el-input :maxlength="20" placeholder="请输入商户名称" v-model="ruleForm.bmp_name"></el-input>
            </el-form-item>
          </el-row>
          <el-row :gutter="24">
            <el-form-item label="银联子商户号:" prop="bmp_mid">
              <el-input v-model="ruleForm.bmp_mid" placeholder="请输入银联子商户号"></el-input>
            </el-form-item>
          </el-row>
          <el-row :gutter="24">
            <el-form-item label="联系人" prop="bmp_contacts">
              <el-input placeholder="请输入联系人" v-model="ruleForm.bmp_contacts"></el-input>
            </el-form-item>
          </el-row>
          <el-row :gutter="24">
            <el-form-item label="联系电话:" prop="bmp_phone">
              <el-input placeholder="请输入联系电话" :maxlength="11" v-model="ruleForm.bmp_phone"></el-input>
            </el-form-item>
          </el-row>
          <el-row :gutter="24">
            <el-form-item label="商户登录名(手机号):" prop="bmp_login_id">
              <el-input :maxlength="11" placeholder="请输入商户登录名(手机号)" v-model="ruleForm.bmp_login_id"></el-input>
            </el-form-item>

          </el-row>
          <el-row :gutter="24">
            <el-form-item label="商户初始密码:" prop="bmp_passwd">
              <el-input placeholder="商户初始密码" v-model="ruleForm.bmp_passwd"></el-input>
            </el-form-item>
          </el-row>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="resetForm('ruleForm')">关 闭</el-button>
        <el-button type="primary" @click="submitData('ruleForm')">确 定</el-button>
      </span>
    </el-dialog>
  </div>

</template>

<script>
  export default {
    data() {
      return {
        msgData:[],
        ruleForm: {
          bmp_name: '',//商户名称
          bmp_mid: "",//银联子商户号
          bmp_login_id: "",//商户登录名
          bmp_passwd: '123456',//商户初始密码
          bmp_phone: '',//联系电话
          bmp_contacts: '',//联系人
        },
        // 商户信息
        merchantInformation: {
          dataMsg: [],
          obj: [],
          name: "xxxxxx",
          accountNumber: "xxxxxx",
          dialogVisible: false,
          value: "",
          value1: [],
          msg: [
            { value: '选项1', label: '黄金糕' },
            { value: '选项2', label: '双皮奶' },
            { value: '选项3', label: '蚵仔煎' },
            { value: '选项4', label: '龙须面' },
            { value: '选项5', label: '北京烤鸭' }
          ],
        },
        //表单校验
        rules: {
          bmp_name: [
            { pattern:/^[\u4E00-\u9FA5A-Za-z0-9]*$/, message: '商户名称不可输入空格、特殊符号', trigger: 'blur' },
            { required: true, message: '请输入商户名称', trigger: 'blur' }
          ],
          bmp_mid: [
            { pattern:/^[A-Za-z0-9]{15}$/, message: '请输入银联子商户号（15位）', trigger: 'blur' },
            { required: true, message: '请输入银联子商户号', trigger: 'blur' }
          ],
          bmp_login_id: [
            { pattern:/^[0-9]{11}$/, message: '请输入手机号码', trigger: 'blur' },
            { required: true, message: '请输入商户登录名', trigger: 'blur' },
            { min: 11, max: 11, message: '长度在 11 个字符', trigger: 'blur' }
          ],
          bmp_passwd: [
            { required: true, message: '请输入商户初始密码', trigger: 'blur' }
          ],
          bmp_phone: [
            { pattern:/^[0-9]{11}$/, message: '请输入手机号码', trigger: 'blur' },
            { required: true, message: '请输入联系电话', trigger: 'blur' },
            { min: 11, max: 11, message: '长度在 11 个字符', trigger: 'blur' }
          ],
          bmp_contacts: [
            { required: true, message: '请输入联系人', trigger: 'blur' }
          ],
        },
        // 新增商户
        newlyAdded: {
          name: "xxxxxx",
          accountNumber: "xxxxxx",
          dialogVisible: false,
          value: "",
          value1: [],
          msg: [
            { radio: '2', name: '满减券推广' },
            { radio: '2', name: '商品推广' },
          ],
          options: [
            { value: '选项1', label: '黄金糕' },
            { value: '选项2', label: '双皮奶' },
            { value: '选项3', label: '蚵仔煎' },
            { value: '选项4', label: '龙须面' },
            { value: '选项5', label: '北京烤鸭' }
          ],
        },
        // 表格菜单
        tableMsg: [
          { key: 'bmp_name', name: '商户名称' },
          { key: 'partner_bind_device_status', name: '是否已绑定设备' },
          { key: 'bmp_mid', name: '银联子商户号' },
          { key: 'bmp_login_id', name: '登录账号',type:'1' }
        ],
        equipmentManagement: {
          //回显数据
          msg: [],
          //列表数据
          tableData: [],
          //弹出层
          dialogVisible: false,
        },
        StateManagement: {
          //单选框选项
          radio: '1',
          input: '',
          //弹出层
          dialogVisible: false,
        },
        //表格头样式
        headerStyle: { 'background': '#4B86BC', 'color': '#ffffff', 'borderRight': '1px solid #E3E5EB', 'padding': '0px', 'height': '45px', 'line-height': '45px' },
        //表格数据
        tableData: [],
        currentCompanyId: '',
      }
    },
    created() {
      this.Select_industry()
      this.bank_commodity()
    },
    watch:{
      'equipmentManagement.dialogVisible'(value){
        this.msgData=[]
      }
    },
    methods: {
      /**
       * 查询商户可选行业
      */
      Select_industry() {
        let that = this;
        let _jsondata_ = {
          obj_bank_center_id: this.customGlobal.userInfoExt.cui_bank_center_id,
          obj_province_id: this.customGlobal.userInfoExt.cui_province_id,
          obj_city_id: this.customGlobal.userInfoExt.cui_city_id,
          obj_county_id: this.customGlobal.userInfoExt.cui_county_id,
          obj_station_id: this.customGlobal.userInfoExt.cui_station_id,
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.cooperative_channel_providers_list_query,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          console.log("异步调用成功返回:" + JSON.stringify(res));
          if (res.data.status == 'success') {
            that.tableData = res.data.rows;
            loading.close();
          } else {
            that.tableData = [];
            console.info("商户查询失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          console.log("异步调用出错:" + JSON.stringify(error));
          loading.close();
          that.tableData = [];
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '商户查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          that.tableData = [];
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '商户查询失败！', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },
      /**
       *  已合作渠道商中的新增渠道商户中的保存
        * @param //bmp_mid
        * @param //bmp_name
        * @param //bmp_login_id
        * @param //bmp_passwd
        * @param //bmp_contacts
        * @param //bmp_phone
        * @param //bmp_bank_center_id
        * @param //bmp_province_id
        * @param //bmp_city_id
        * @param //bmp_county_id
        * @param //bmp_station_id
        * @param //bmp_cui_id
      */
      submitData(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.submitForm()
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      submitForm() { 
        let that = this;
        let _jsondata_ = {
          bmp_type:'1',
          bmp_mid: this.ruleForm.bmp_mid,
          bmp_name: this.ruleForm.bmp_name,
          bmp_login_id: this.ruleForm.bmp_login_id,
          bmp_passwd: this.customGlobal.md5(this.ruleForm.bmp_passwd),
          bmp_contacts: this.ruleForm.bmp_contacts,
          bmp_phone: this.ruleForm.bmp_phone,
          bmp_bank_center_id: this.customGlobal.userInfoExt.cui_bank_center_id,
          bmp_province_id: this.customGlobal.userInfoExt.cui_province_id,
          bmp_city_id: this.customGlobal.userInfoExt.cui_city_id,
          bmp_county_id: this.customGlobal.userInfoExt.cui_county_id,
          bmp_station_id: this.customGlobal.userInfoExt.cui_station_id,
          bmp_cui_id: this.customGlobal.userInfoExt.cui_cui_id,
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.cooperative_channel_providers_insert,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          console.log("异步调用成功返回:" + JSON.stringify(res));
          if (res.data.status == 'success') {
            this.Select_industry()
            this.newlyAdded.dialogVisible = false
            this.resetForm('ruleForm')
            loading.close();
            this.$message({
              type: 'success',
              showClose: true,
              duration: 3000,
              dangerouslyUseHTMLString: true,
              message: '<span style="color:black;">商户新增成功</span>'
            });
          } else {
            that.tableData = [];
            console.info("商户查询失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          console.log("异步调用出错:" + JSON.stringify(error));
          loading.close();
          that.tableData = [];
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '商户查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          that.tableData = [];
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '商户查询失败！', {
            confirmButtonText: '确定',
            callback: action => {
            }
          });
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
        this.newlyAdded.dialogVisible = false
      },
      resetForm1(formName) {
        this.equipmentManagement.dialogVisible = false
      },

      /**
       * 获取设备列表
      */
      equipmentGetData(value) {
        this.equipmentManagement.msg = value
        let that = this;
        let _jsondata_ = {
          bmp_uuid: value.bmp_uuid,
          obj_bank_center_id: this.customGlobal.userInfoExt.cui_bank_center_id,
          obj_province_id: this.customGlobal.userInfoExt.cui_province_id,
          obj_city_id: this.customGlobal.userInfoExt.cui_city_id,
          obj_county_id: this.customGlobal.userInfoExt.cui_county_id,
          obj_station_id: this.customGlobal.userInfoExt.cui_station_id,
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.channel_each_device_rate_query,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          console.log("异步调用成功返回:" + JSON.stringify(res));
          if (res.data.status == 'success') {
            this.equipmentManagement.dialogVisible = true
            this.equipmentManagement.tableData = res.data.device_info
            this.equipmentManagement.tableData.forEach(element => {
              let vfShow = element.bmp_name == '' ? true : false

            });
            loading.close();
          } else {
            that.tableData = [];
            console.info("查询失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          console.log("异步调用出错:" + JSON.stringify(error));
          loading.close();
          that.tableData = [];
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          that.tableData = [];
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '查询失败！', {
            confirmButtonText: '确定',
            callback: action => {
            }
          });
        });
      },
      /**
       * 设备列表解绑
      */
      channelClick(value) {
        this.equipmentManagement.tableData[value.$index].mbmp_fee = ""
        this.equipmentManagement.tableData[value.$index].bmp_name = ''
        this.equipmentManagement.tableData[value.$index].bmp_uuid = ''
        let show = this.msgData.every(res=>{
          return res.di_uuid !== this.equipmentManagement.tableData[value.$index].di_uuid
        })
        if (show) {
          this.msgData.push(this.equipmentManagement.tableData[value.$index])
        }else{
          let data = this.msgData.filter(res=>{
            return res.di_uuid !== this.equipmentManagement.tableData[value.$index].di_uuid
          })
          this.msgData = data
          this.msgData.push(this.equipmentManagement.tableData[value.$index])
        }
        
        console.log(this.msgData,'this.msgData');
      },
      /**
       * 设备列表绑定
      */
      bindingClick(value) {
        this.equipmentManagement.tableData[value.$index].mbmp_fee = ""
        this.equipmentManagement.tableData[value.$index].bmp_name = this.equipmentManagement.msg.bmp_name
        this.equipmentManagement.tableData[value.$index].bmp_uuid = this.equipmentManagement.msg.bmp_uuid
        let show = this.msgData.every(res=>{
          return res.di_uuid !== this.equipmentManagement.tableData[value.$index].di_uuid
        })
        if (!show) {
          let data = this.msgData.filter(res=>{
            return res.di_uuid !== this.equipmentManagement.tableData[value.$index].di_uuid
          })
          this.msgData = data
        }
        console.log(this.msgData,'this.msgData');
      },
      /**
       * 提交已合作渠道商中的绑定设备及费率
      */
      submitList(formName) {
        this.$confirm('是否操作当前渠道商设备列表?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.submitState()
        })
      },
      submitState() {
        let that = this;
        let msg = this.equipmentManagement.tableData.filter((res) => {
          return res.bmp_uuid == this.equipmentManagement.msg.bmp_uuid
        })
        let _jsondata_ = {
          mbmp_partner_uuid: this.equipmentManagement.msg.bmp_uuid,
          merPartnerBindDeviceInfoList: msg
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.channel_each_device_rate_update,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          console.log("异步调用成功返回:" + JSON.stringify(res));
          if (res.data.status == 'success') {
            this.msgData.forEach(res=>{
              res.show=true
            })
            let value = this.msgData.concat(msg)
            this.platform_service_fee_submit1(value)
            this.Select_industry()
            this.equipmentManagement.dialogVisible = false
            loading.close();
            this.$message({
              type: 'success',
              showClose: true,
              duration: 3000,
              dangerouslyUseHTMLString: true,
              message: '<span style="color:black;">修改成功</span>'
            });
          } else {
            that.tableData = [];
            console.info("修改失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          console.log("异步调用出错:" + JSON.stringify(error));
          loading.close();
          that.tableData = [];
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '修改失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          that.tableData = [];
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '修改失败', {
            confirmButtonText: '确定',
            callback: action => {
            }
          });
        });
      },
      //保留小数点后两位
      inputChange(value, key) {
        value[key] = value[key].replace(/[^\d.]/g, "");  //清除“数字”和“.”以外的字符   
        value[key] = value[key].replace(/\.{2,}/g, "."); //只保留第一个. 清除多余的   
        value[key] = value[key].replace(".", "$#$").replace(/\./g, "").replace("$#$", ".");
        value[key] = value[key].replace(/^(\-)*(\d+)\.(\d\d).*$/, '$1$2.$3');//只能输入两个小数
        if (value[key].indexOf(".") < 0 && value[key] != "") {//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
          value[key] = parseFloat(value[key]);
          value[key] = value[key]+''
          if (value[key].length>2) {
            value[key]=value[key].slice(0,2)
          }
        }
      },
      //转13位id
      bank_commodity() {
        let that = this;
        let _jsondata_ = {
          companyId: this.customGlobal.userInfoExt.cui_station_id,
        };
        const loading = this.$loading({
          lock: true,
          text: '',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        var s1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.bank_transcoding,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        s1.then((res) => {
          if (res.data.status == 'success') {
            loading.close();
            that.currentCompanyId = res.data.currentCompanyId
          } else {
            console.info("转码失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          loading.close();
          this.$alert(JSON.stringify(error.message), '转码出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          this.$alert(JSON.stringify(res.message), '转码出错', {
            confirmButtonText: '确定',
            callback: action => {
            }
          });
        });
      },
      //渠道商服务费设置弹窗保存
      platform_service_fee_submit1(value) {
        let that = this;
        let detail = value.map(res=>{
          return {
            device_uuid: res.di_uuid?res.di_uuid:'0',
            obj_fee: res.mbmp_fee?res.mbmp_fee:'0',
            obj_id:res.show?'0':this.equipmentManagement.msg.bmp_mid,
            bank_company_alias_id: this.currentCompanyId,
          }
        })
        let _jsondata_ = {
          flag: '2_2',
          detail:detail
        };
        var d2 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.postBank13CompanyIdAndDeviceUnionConfigExt,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        d2.then((res) => {
          if (res.data.status == 'success') {

          } else {
            console.info("平台服务费设置失败");
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          this.$alert(JSON.stringify(error.message), '平台服务费设置出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          this.$alert(JSON.stringify(res.message), '平台服务费设置出错', {
            confirmButtonText: '确定',
            callback: action => { }
          });
        });
      },




      /**
       * 模块弹窗
      */
      PopupControl(value, name, i) {
        console.log(value.row);
        name.dialogVisible = true
        name.name = value.row.mbmi_mer_name
        name.obj = value.row
      },



      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => { });
      },
      /**------分页-------start----*/
      handleSizeChange(val) {
        // console.log(`每页 ${val} 条`);
        this.queryParam.pageSize = val;
        this.queryValveCurrentData();
      },
      handleCurrentChange(val) {
        // console.log(`当前页: ${val}`);
        this.queryParam.currentPage = val;
        this.queryValveCurrentData();
      },
    },
  }
</script>
<style>
  .box>>>.el-table__fixed-right::before,
  .el-table__fixed::before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: #EBEEF5;
    z-index: 4;
  }

  .box1 {
    padding-right: 20px;
  }
</style>


